@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
@import url(https://fonts.googleapis.com/css2?family=Lora:ital@1&display=swap" rel="stylesheet);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo{
    font-family: 'Josefin Sans', cursive;

    cursor: pointer;
    padding: 10px;
    font-size: 1em;
}

.nav-list li:hover{
    border-bottom: 2px solid orange;
}
.nav-list{
    display: flex;
    font-size: 1em;
    align-items: center;
    justify-content: space-evenly;

}
.nav-list li{
    list-style: none;
    cursor: pointer;
    padding: 10px;
    border: none;
    font-family: 'Lora', serif;

    
}
.search {
    display: flex;
    width: 30%;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
    font-size: 1.2em;
}

.hide,.show{
    width: 0%;
    margin: 5px;
    margin-left: 25px;
    transition: 2s;
    padding: 10px;
    outline: none;
    border: none;
}
.show{
    width: 300px;
    border: solid 1px black;
    
}

.youtube{
    color: red;
}
.instagram{
    color: rgb(255, 100, 126);

}
.facebook{
    color: rgb(5, 5, 66);

}
.contact{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.contact li{
    cursor: pointer;
    font-size: 1.2em;
    padding: 20px;
    list-style: none;
}
.navbar
{display: flex;
justify-content: space-evenly;
align-items: center;
font-size: 1.2em;
}
